import React from 'react';
import { Link } from 'react-router-dom';
import Grid from './Grid';
import logo from '../Asset/images/Logo.png';

const footerAboutLink = [
  {
    display: 'Gii thiu',
    path: '/about',
  },
  {
    display: 'Lin h',
    path: '/about',
  },
  {
    display: 'Tuyn dng',
    path: '/about',
  },
  {
    display: 'Tin tc',
    path: '/about',
  },
  {
    display: 'H thng ca hng',
    path: '/about',
  },
];

const footerCustomeLink = [
  {
    display: 'Chnh sch i tr',
    path: '/about',
  },
  {
    display: 'Chnh sch bo hnh',
    path: '/about',
  },
  {
    display: 'Chnh sch hon tin',
    path: '/about',
  },
];
const Footer = () => {
  return (
    <footer className="container footer">
      <Grid
        col={4}
        mdCol={2}
        smCol={1}
        gap={10}
      >
        <div className="">
          <div className="footer__title">Tng i h tr</div>
          <div className="footer__content">
            <p>
              Lin h t hng: <strong>0399817202</strong>
            </p>
            <p>
              Thc mc n hng: <strong>0399817202</strong>
            </p>
            <p>
              Gp , khiu ni: <strong>0399817202</strong>
            </p>
          </div>
        </div>
        <div>
          <div className="footer__title">V Yolo</div>
          <div className="footer__content">
            {footerAboutLink.map((item, index) => {
              return (
                <p key={index}>
                  <Link to={item.path}>{item.display}</Link>
                </p>
              );
            })}
          </div>
        </div>

        <div>
          <div className="footer__title">Chm sc khch hng</div>
          <div className="footer__content">
            {footerCustomeLink.map((item, index) => {
              return (
                <p key={index}>
                  <Link to={item.path}>{item.display}</Link>
                </p>
              );
            })}
          </div>
        </div>

        <div className="footer__about">
          <p>
            <Link to="/">
              <img
                src={logo}
                alt=""
                className="footer__logo"
              />
            </Link>
          </p>
          <p>
            Hng n mc tiu mang li nim vui n mc mi ngy cho hng triu
            ngi tiu dng Vit. Hy cng Yolo hng n mt cuc sng nng
            ng, tch cc hn."t s hi lng ca khch hng l u tin s 1
            trong mi suy ngh hnh ng ca mnh l s mnh, l trit l,
            chin lc.. lun cng YOLO tin bc
          </p>
        </div>
      </Grid>
      <Grid
        col={1}
        mdCol={1}
        smCol={1}
      >
        <div className="footer__end">
          <span>
            @ Bn quyn thuc v <strong>Yolo.vn</strong> All right reserved
          </span>
        </div>
      </Grid>
    </footer>
  );
};

export default Footer;
