import React from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { Link } from 'react-router-dom';
import axios from 'axios';
const SignInForm = () => {
  const formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      password: '',
    },
    validationSchema: Yup.object({
      name: Yup.string()
        .required('Vui lng nhp trng ny')
        .min(4, 'Tn qu ngn, yu cu t nht 4 k t'),
      email: Yup.string()
        .required('Vui lng nhp trng ny')
        .matches(
          // eslint-disable-next-line
          /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/,
          'a ch email khng hp l'
        ),
      password: Yup.string()
        .required('Vui lng nhp trng ny')
        .matches(
          /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*()_+])[A-Za-z\d][A-Za-z\d!@#$%^&*()_+]{7,19}$/,
          'Mt khu yu cu 7-19 k t, cha t nht mt ch ci, mt s v mt k t c bit'
        ),
    }),
    onSubmit: async (values)=>{
      try {
        await axios.post('/user/login', { ...values});
  
        localStorage.setItem('firstLogin', true);
  
        window.location.href = '/';
      } catch (err) {
        alert(err.response.data.msg);
      }
    }
  });
  return (
    <section>
      <form
        className="infoform"
        onSubmit={formik.handleSubmit}
      >
        <label> Tn ng nhp</label>
        <input
          type="text"
          id="name"
          name="name"
          placeholder="H V Tn"
          value={formik.values.name}
          onChange={formik.handleChange}
        />
        {formik.errors.name && (
          <p className="errorMsg"> {formik.errors.name} </p>
        )}
        <label> Email</label>
        <input
          type="email"
          id="email"
          name="email"
          placeholder="Email "
          value={formik.values.email}
          onChange={formik.handleChange}
        />
        {formik.errors.email && (
          <p className="errorMsg"> {formik.errors.email} </p>
        )}

        <label> Mt khu </label>
        <input
          type="text"
          id="password"
          name="password"
          placeholder="Mt khu"
          value={formik.values.password}
          onChange={formik.handleChange}
        />
        {formik.errors.password && (
          <p className="errorMsg"> {formik.errors.password} </p>
        )}
        <div className="infoform__control">
          <Link
            to="/signup"
            className="infoform__control__btn"
          >
            ng k
          </Link>

          <button type="submit"> Tip tc </button>
        </div>
      </form>
    </section>
  );
};

export default SignInForm;
